body {
  font-family: 'Arial', sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

header {
  background-color: #a178ff;
  color: rgba(237, 209, 255, 0.979);
  padding: 1rem;
  text-align: center;
}

nav ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

nav li {
  display: inline;
  margin-right: 10px;
}

nav a {
  color: rgba(0, 0, 0, 0.609);
  text-decoration: none;
}

section {
  padding: 2rem;
  text-align: center;
}

footer {
  background-color: #a178ff;
  color: rgb(31, 19, 35);
  padding: 1rem;
  text-align: center;
  position: fixed;
  bottom: 0;
  width: 100%;
}